import { useState } from 'react';
import { auth, googleProvider, signInWithEmailAndPassword, signInWithPopup, createUserWithEmailAndPassword } from '../config/firebase';
import { LoginForm } from '../components/auth/LoginForm';
import { TextButton } from '../components/form/TextButton';
import { useAuth } from '../hooks/useAuth';

export function Login({ onLogin, onCriarConta }) {
  const [email, setEmail] = useState('');
  const [senha, setSenha] = useState('');
  const [confirmarSenha, setConfirmarSenha] = useState('');
  const [erro, setErro] = useState('');
  const [carregando, setCarregando] = useState(false);
  const [modo, setModo] = useState('escolha'); // escolha, login, registro, anonimo
  const [senhaVisivel, setSenhaVisivel] = useState(false);
  const { login, loginWithGoogle } = useAuth();

  const handleLogin = async (e) => {
    e.preventDefault();
    setErro('');
    setCarregando(true);

    try {
      const user = await login(email, senha);
      onLogin({
        email: user.email,
        uid: user.uid,
        nome: user.displayName
      });
    } catch (error) {
      console.error(error);
      setErro('Email ou senha incorretos');
    } finally {
      setCarregando(false);
    }
  };

  const handleRegistro = async (e) => {
    e.preventDefault();
    if (senha !== confirmarSenha) {
      setErro('As senhas não coincidem');
      return;
    }
    setErro('');
    setCarregando(true);

    try {
      const resultado = await createUserWithEmailAndPassword(auth, email, senha);
      onLogin({
        email: resultado.user.email,
        uid: resultado.user.uid,
        nome: resultado.user.displayName
      });
    } catch (error) {
      console.error(error);
      setErro('Erro ao criar conta');
    } finally {
      setCarregando(false);
    }
  };

  const handleGoogleLogin = async () => {
    setErro('');
    try {
      const user = await loginWithGoogle();
      onLogin({
        email: user.email,
        uid: user.uid,
        nome: user.displayName
      });
    } catch (error) {
      console.error(error);
      if (error.code !== 'auth/popup-closed-by-user') {
        setErro('Erro ao fazer login com Google');
      }
    }
  };

  const handleAnonimo = () => {
    localStorage.setItem('modo', 'anonimo');
    onLogin({ 
      email: 'anonimo@irai.app',
      uid: 'anonimo',
      nome: 'Anônimo'
    });
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-white py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md w-full">
        <div className="bg-white p-8 rounded-xl shadow-sm border border-gray-200">
          <div className="text-center">
            <div className="flex justify-center mb-4">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-12 w-12 text-emerald-600" viewBox="0 0 24 24" fill="currentColor">
                <path d="M12 3L1 9l4 2.18v6L12 21l7-3.82v-6l2-1.09V17h2V9L12 3m6.82 6L12 12.72L5.18 9L12 5.28L18.82 9M17 15.99l-5 2.73l-5-2.73v-3.72L12 15l5-2.73v3.72z"/>
              </svg>
            </div>
            <h1 className="text-3xl font-bold bg-gradient-to-r from-emerald-600 to-emerald-500 bg-clip-text text-transparent">
              Iraí
            </h1>
            <p className="mt-2 text-sm text-gray-600">
              Não tem uma conta?{' '}
              <button 
                onClick={onCriarConta}
                className="font-medium text-emerald-600 hover:text-emerald-500 transition-colors"
              >
                Criar conta
              </button>
            </p>
          </div>

          <div className="mt-8">
            <LoginForm
              email={email}
              setEmail={setEmail}
              senha={senha}
              setSenha={setSenha}
              onSubmit={handleLogin}
              erro={erro}
              carregando={carregando}
              onGoogleLogin={handleGoogleLogin}
            />
          </div>
        </div>
      </div>
    </div>
  );
} 